
const SCROLL_TYPE = {
  H_LEFT: Symbol(''),
  H_RIGHT: Symbol(''),
  V_TOP: Symbol(''),
  V_BOTTOM: Symbol(''),
  UN: Symbol(''),
};

class Scroll {

  constructor({
    fixed,
  }) {
    this.ri = fixed.fxTop + 1;
    this.ci = fixed.fxLeft + 1;
    this.x = 0;
    this.y = 0;
    this.type = SCROLL_TYPE.UN;
  }
}

export {
  Scroll, SCROLL_TYPE,
};
